    methods: {
        getLastest(data){
            this.lastestAct = data
        },
        getNavContent(value,i){
            this.nav.forEach((value) => {
                value.active = false
            })
            this.nav[i].active = true
            $.post('/getLastest',{
                itemType:value
            }).then(res => {
                this.lastestAct = res.data
            })
        },
        getMore(){
            this.page ++
            $.post('/getMore',{
                page:this.page
            }).then(res => {
                console.log(res.data)
                this.lastestAct.push(...res.data)
                console.log(this.lastestAct)
            }).catch(err => {
                this.$message({
                    type:'warning',
                    message:''
                })
            })
        },
        topicChange(){
            this.$axios.get('/topicChange').then(res => {
                this.hotTopic = res.data
            }).catch(err => {
                this.$message({
                    type:'warning',
                    message:''
                })
            })
        },
        funPerChange(){
            this.$axios.get('/funPerChange').then(res => {
                this.funPerson = res.data
            }).catch(err => {
                this.$message({
                    type:'warning',
                    message:''
                })
            })
        },
        follow(){
            this.$axios.post('/follow',{
                name:this.funPerson.name
            }).then(res => {
                this.$message({
                    type:'success',
                    message:''
                })
                this.funPerson.followStatu = ''
            }).catch(err => {
                this.$message({
                    type:'warning',
                    message:''
                })
            })
        }
    },
    components: {
        publish,
        activity
    }
